import { array } from "yup";
import * as dashboardAPI from "./DashboardCrud.js";
import { DashboardSlice } from "./DashboardSlice";

const { actions } = DashboardSlice;

export const getManufacturingMethodData = () => (dispatch) => {
  dispatch(actions.startCall());
  return dashboardAPI
    .getManufacturingMethodData()
    .then((res) => {
      const DashboardData = res.data;
      dispatch(
        actions.setManufacturingMethodData({
          manufacturingMethodData: DashboardData.data,
        })
      );
    })
    .catch((err) => {
      console.log(err);
    });
};

