function onlyUnique(value, index, self) {
  return self.indexOf(value) === index;
}

//ManufacturingMethodData

export const getProcessNameFilteredData =
  ({ rawData, outboundTimeFilter, itemIdFilter, itemNameFilter }) =>
  (dispatch) => {
    let filterDataTemp = [];
    rawData.map((datum) => {
      filterDataTemp.push(datum);
    });
    
    if (outboundTimeFilter && outboundTimeFilter[0] && outboundTimeFilter[1]) {
      filterDataTemp = filterDataTemp.filter(
        (datum) =>
          new Date(datum.出站時間) >= outboundTimeFilter[0] &&
          new Date(datum.出站時間) <=
            new Date(
              new Date(
                new Date(outboundTimeFilter[1]).getTime() + 24 * 60 * 60 * 999
              )
            )
      );
    }
    if (itemIdFilter && itemIdFilter !== "all") {
      filterDataTemp = filterDataTemp.filter(
        (datum) => datum.料號 === itemIdFilter
      );
    }

    if (itemNameFilter && itemNameFilter !== "all") {
      filterDataTemp = filterDataTemp.filter(
        (datum) => datum.品名 === itemNameFilter
      );
    }

    dispatch(
      actions.setProcessNameFilteredData({
        processNameFilteredData: filterDataTemp,
      })
    );
  };

