export const getProcessNameOptions =
  ({ filteredData }) =>
  (dispatch) => {
    let processNameOptionsTemp = [];
    filteredData.map((datum) => {
      if (datum.製程敘述) {
        processNameOptionsTemp.push(datum.製程敘述);
      }
    });

    let options = processNameOptionsTemp.filter(onlyUnique).sort();
    processNameOptionsTemp = [{ label: "全部", value: "all" }];
    options.map((option) => {
      processNameOptionsTemp.push({ label: option, value: option });
    });

    dispatch(
      actions.setProcessNameOptions({
        processNameOptions: processNameOptionsTemp,
      })
    );
  };

