//機時平均
function mtAverage(array) {
  let mtTemp = [];
  array.map((datum) => {
    mtTemp.push(datum["機時(秒)/pcs"]);
  });

  var mtAvg = 0;
  for (let i = 0; i < mtTemp.length; i++) {
    mtAvg += mtTemp[i];
  }
  if (mtTemp.length !== 0) {
    mtAvg = Math.round((mtAvg / mtTemp.length) * 100) / 100;
    return mtAvg;
  } else {
    return 0;
  }
}

//人時平均
function ptAverage(array) {
  let ptTemp = [];
  array.map((datum) => {
    ptTemp.push(datum["人時(秒)/pcs"]);
  });
  var ptAvg = 0;
  for (let i = 0; i < ptTemp.length; i++) {
    ptAvg += ptTemp[i];
  }
  if (ptTemp.length !== 0) {
    ptAvg = Math.round((ptAvg / ptTemp.length) * 100) / 100;
    return ptAvg;
  } else {
    return 0;
  }
}

//排序
function sortArray(array) {
  var temp = 0;
  if (array.length > 1)
    for (var i = 0; i < array.length; i++) {
      for (var j = i; j < array.length; j++) {
        if (array[j].value > array[i].value) {
          temp = array[j];
          array[j] = array[i];
          array[i] = temp;
        }
      }
    }
  return array;
}

// D1charts
export const getD1C1Series =
  ({ filteredData }) =>
  (dispatch) => {
    let filteredDataTemp = [];
    filteredData.map((datum) => {
      filteredDataTemp.push(datum);
    });

    filteredDataTemp = filteredDataTemp.filter(
      (datum) => datum["助撕/拉拔"] !== "None"
    );
    let mtSeriesData = [];
    let mtSeriesName = [];
    filteredDataTemp.map((datum) => {
      mtSeriesName.push(datum["助撕/拉拔"]);
    });
    mtSeriesName = mtSeriesName.filter(onlyUnique).sort();

    //助撕
    let tearTemp = [];
    tearTemp = filteredDataTemp.filter(
      (datum) => datum["助撕/拉拔"] === "助撕"
    );
    if (tearTemp.length > 0) {
      mtSeriesData.push(mtAverage(tearTemp));
    }

    //拉拔
    let drawingTemp = [];
    drawingTemp = filteredDataTemp.filter(
      (datum) => datum["助撕/拉拔"] === "拉拔"
    );

    if (drawingTemp.length > 0) {
      mtSeriesData.push(mtAverage(drawingTemp));
    }

    let seriesTemp = [];
    for (let i = 0; i < mtSeriesName.length; i++) {
      seriesTemp.push({
        name: mtSeriesName[i],
        value: mtSeriesData[i],
      });
    }

    sortArray(seriesTemp);

    let sortName = [];
    seriesTemp.map((datum) => {
      sortName.push(datum.name);
    });
    let sortData = [];
    seriesTemp.map((datum) => {
      sortData.push(datum.value);
    });

    let c1SeriesTemp = { name: sortName, data: sortData };

    dispatch(
      actions.setD1C1Series({
        d1c1Series: c1SeriesTemp,
      })
    );
  };

