import React from "react";
import { Helmet } from "react-helmet";
import { Redirect, Switch, Route } from "react-router-dom";

import { Dashboard1Page } from "./pages/dashboard1/Dashboard1Page";
import { Dashboard2Page } from "./pages/dashboard2/Dashboard2Page";
import { Dashboard3Page } from "./pages/dashboard3/Dashboard3Page";
import { Dashboard4Page } from "./pages/dashboard4/Dashboard4Page";
import { Dashboard5Page } from "./pages/dashboard5/Dashboard5Page";
export default function ChartsPage() {
  return (
    <>
      <Helmet>
        <title>T-Global 圖表</title>
      </Helmet>
      <Switch>
        <Redirect exact from="/charts" to="/charts/chart1" />
        <Route path="/charts/chart1" component={Dashboard1Page} />
        <Route path="/charts/chart2" component={Dashboard2Page} />
        <Route path="/charts/chart3" component={Dashboard3Page} />
        <Route path="/charts/chart4" component={Dashboard4Page} />
        <Route path="/charts/chart5" component={Dashboard5Page} />
        <Redirect to="/error/error-v1" />
      </Switch>
    </>
  );
}
