import React, { useEffect, useState } from "react";
import "../../../../../charts/chartsStyle.css"
import Card from "@material-ui/core/Card";

import Chart from "react-apexcharts";
import { CardContent, Typography } from "@material-ui/core";
import { Dashboard3ChartSkeletion } from "../Dashboard3CardSkeletion";

function getChartHeight(length) {
  var height = 280;

  if (length < 6) {
    return height;
  } else {
    height = height * (length / 6);
  }
  return height;
}

export function Chart3_2({ data,loading }) {
  const [series, setSeries] = useState([]);
  const [options, setOptions] = useState(null);
  const [categories, setCategories] = useState([]);
  const [height, setHeight] = useState(["350"]);

  useEffect(() => {
    if (data  && typeof data !== "undefined") {
      setSeries([{ name: "人時(秒)/pcs", data: data.details.slice(0, 19) }]);
      setCategories(data.col.slice(0, 19))
    }
    // if (data) {
    //   setCategories(data.col.slice(0, 99));
    // }
  }, [data]);

  useEffect(() => {
    setOptions({
      chart: {
        type: "bar",
        height: 350,
        offsetY: -5,
        toolbar: {
          show: false,
        },
        animations:{
          enabled:false
        }
      },
      // colors: ["#FFAD21","#FF8F35"],
      colors: ["#FF8A20","#FFB664"],
      plotOptions: {
        bar: {
          borderRadius: 4,
          horizontal: true,
          distributed: true,
          dataLabels: {
            position: 'bottom',
          },
        },
      },
      dataLabels: {
        enabled: true,
        offsetX:0,
        textAnchor: 'start',
        style: {
          fontSize: "16px",
          color: "#2F2F2F",
        },
        formatter: function (val, opt) {
          return opt.w.globals.labels[opt.dataPointIndex] + "  :  " + val
        },
        dropShadow: {
          enabled: true,
        }
      },

      xaxis: {
        categories: categories,
        labels: {
          hideOverlappingLabels: true,
          style: {
            fontSize: "18px",
            colors: "#f0eafb",
          },
          offsetY: 6,
        },
        tickAmount: 6,
        axisTicks: {
          offsetY: -1,
        },
      },
      yaxis: {
        show:false,
        labels: {
          style: {
            fontSize: "18px",
            colors: "#f0eafb",
          },
        },
      },
      legend:{
        show:false
      }
    });
  }, [categories]);

  useEffect(() => {
    setHeight(getChartHeight(categories.length));
  }, [categories.length]);

  return (
    <>
      <Card raised={true}>
        <CardContent>
          <Typography
            style={{ textAlign: "center", height: "30px", fontSize: "20px" }}
          >
            人時(秒)/pcs
          </Typography>
          {loading ? (
            <Dashboard3ChartSkeletion />
          ) : (
            <div style={{ height: "260px", overflow: "auto" }}>
              {options ? (
                <Chart
                  options={options}
                  series={series}
                  type="bar"
                  height={height}
                  width={"98%"}
                />
              ) : null}
            </div>
          )}
          <Typography
            style={{ textAlign: "center", height: "20px", fontSize: "15px",padding:"10px" }}
          >
            平均一片(s)
          </Typography>
        </CardContent>
      </Card>
    </>
  );
}
