import axios from "axios";

const ip = "https://tglobal-rtsftrack.aiv.com.tw/api/updatedata";
const chartip = "https://tglobal-rtsftrack.aiv.com.tw/api/dashboard";
// const ip = "https://bi-dashboard.aiv.com.tw/api/dashboard";

export const getManufacturingMethodData = () => {
  let data = axios.get(ip + "/Manufacturing_method_json");
  return data;
};

export const getProductnameTimeData = () => {
  let data = axios.get(ip + "/Productname_time_json");
  return data;
};

export const getProcessTimeData = () => {
  let data = axios.get(ip + "/Process_time_json");
  return data;
};

export const getProductnameTimeChartData = () => {
  let data = axios.get(ip + "/Productname_time_nest_fast");
  return data;
};

export const getDashboard1ChartData = (
  starttime,
  endtime,
  process,
  productnumber,
  productname
) => {
  let data = axios.get(
    // chartip+"/dashboard_1_api/?starttime=2021-11-09&endtime=2021-11-12&process=all&productnumber=all&productname=all"
    chartip +
      "/dashboard_1_api/?starttime=" +
      starttime +
      "&endtime=" +
      endtime +
      "&process=" +
      process +
      "&productnumber=" +
      productnumber +
      "&productname=" +
      productname
  );
  return data;
};

