import React, { useState, useEffect } from "react";
import * as actions from "../../../../../../../_redux/dashboard/DashboardActions";

import { DynamicRadioButtonList } from "../../../../../../../../components/DynamicRadioButtonList";

import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";

import { shallowEqual, useDispatch, useSelector } from "react-redux";


export function Dashboard1Filter4({
  value,
  setValue,
  processNameFilter,
  outboundTimeFilter,
  itemIdFilter,
}) {
  const [name, setName] = useState("全部");

  const dispatch = useDispatch();

  const {
    actionsLoading,
    manufacturingMethodData,
    itemNameOptions,
    itemNameFilteredData,
  } = useSelector(
    (state) => ({
      actionsLoading: state.dashboard.actionsLoading,
      manufacturingMethodData: state.dashboard.manufacturingMethodData,
      itemNameOptions: state.dashboard.itemNameOptions,
      itemNameFilteredData: state.dashboard.itemNameFilteredData,
    }),
    shallowEqual
  );

  useEffect(() => {
    dispatch(
      actions.getItemNameOptions({ filteredData: itemNameFilteredData })
    );
  }, [itemNameFilteredData]);

  useEffect(() => {
    dispatch(
      actions.getItemNameFilteredData({
        rawData: manufacturingMethodData,
        processNameFilter: processNameFilter,
        outboundTimeFilter: outboundTimeFilter,
        itemIdFilter: itemIdFilter,
      })
    );
  }, [
    manufacturingMethodData,
    processNameFilter,
    outboundTimeFilter,
    itemIdFilter,
  ]);

  const handleChange = (event) => {
    console.log(event.target);
    setValue(event.target.value);
    setName(event.target.name);
  };

  return (
    <>
      <Card raised={true}>
        <CardContent>
          <Typography style={{ height: "30px" }}>品名 : {name}</Typography>
          <div style={{ height: "115px", width: "100%" }}>
            <DynamicRadioButtonList
              data={itemNameOptions}
              checked={value}
              handleChange={handleChange}
            />
          </div>
        </CardContent>
      </Card>
    </>
  );
}
