import React, { useState, useEffect } from "react";
import * as actions from "../../../../../../../_redux/dashboard/DashboardActions";

import { DynamicRadioButtonList } from "../../../../../../../../components/DynamicRadioButtonList";


import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControl from "@material-ui/core/FormControl";

import { shallowEqual, useDispatch, useSelector } from "react-redux";



export function Dashboard1Filter3({
  value,
  setValue,
  processNameFilter,
  outboundTimeFilter,
  itemNameFilter,
}) {
  const [name, setName] = useState("全部");
  // const [options, setOptions] = useState([]);
  // const [filteredData, setFilteredData] = useState([]);
  const dispatch = useDispatch();

  const {
    actionsLoading,
    manufacturingMethodData,
    itemIdOptions,
    itemIdFilteredData,
  } = useSelector(
    (state) => ({
      actionsLoading: state.dashboard.actionsLoading,
      manufacturingMethodData: state.dashboard.manufacturingMethodData,
      itemIdOptions: state.dashboard.itemIdOptions,
      itemIdFilteredData: state.dashboard.itemIdFilteredData,
    }),
    shallowEqual
  );

  useEffect(() => {
    dispatch(actions.getItemIdOptions({ filteredData: itemIdFilteredData }));
  }, [itemIdFilteredData]);

  useEffect(() => {
    dispatch(
      actions.getItemIdFilteredData({
        rawData: manufacturingMethodData,
        processNameFilter: processNameFilter,
        outboundTimeFilter: outboundTimeFilter,
        itemNameFilter: itemNameFilter,
      })
    );
  }, [
    manufacturingMethodData,
    processNameFilter,
    outboundTimeFilter,
    itemNameFilter,
  ]);

  const handleChange = (event) => {
    console.log(event.target)
    setValue(event.target.value);
    setName(event.target.name);
  };

  return (
    <>
      <Card raised={true}>
        <CardContent>
          <Typography style={{ height: "30px" }}>料號 : {name}</Typography>
          <div style={{ height: "115px", width: "100%" }}>
            <DynamicRadioButtonList
              data={itemIdOptions}
              checked={value}
              handleChange={handleChange}
            />
          </div>
        </CardContent>
      </Card>
    </>
  );
}
