import React, { useState, useEffect } from "react";
import * as actions from "../../../../../../../_redux/dashboard/DashboardActions";

import { DynamicRadioButtonList } from "../../../../../../../../components/DynamicRadioButtonList";

import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";

import { shallowEqual, useDispatch, useSelector } from "react-redux";


export function Dashboard2Filter1({
  value,
  setValue,
  outboundTimeFilter,
  itemIdFilter,
  itemNameFilter,
}) {
  const [name, setName] = useState("全部");
  const dispatch = useDispatch();

  const {
    actionsLoading,
    manufacturingMethodData,
    processNameOptions,
    processNameFilteredData,
  } = useSelector(
    (state) => ({
      actionsLoading: state.dashboard.actionsLoading,
      manufacturingMethodData: state.dashboard.manufacturingMethodData,
      processNameOptions: state.dashboard.processNameOptions,
      processNameFilteredData: state.dashboard.processNameFilteredData,
    }),
    shallowEqual
  );

  useEffect(() => {
    dispatch(
      actions.getProcessNameOptions({ filteredData: processNameFilteredData })
    );
  }, [processNameFilteredData]);

  useEffect(() => {
    dispatch(
      actions.getProcessNameFilteredData({
        rawData: manufacturingMethodData,
        outboundTimeFilter: outboundTimeFilter,
        itemIdFilter: itemIdFilter,
        itemNameFilter: itemNameFilter,
      })
    );
  }, [
    manufacturingMethodData,
    outboundTimeFilter,
    itemIdFilter,
    itemNameFilter,
  ]);

  const handleChange = (event) => {
    console.log(event.target);
    setValue(event.target.value);
    setName(event.target.name);
  };

  return (
    <>
 <Card raised={true}>
        <CardContent>
          <Typography style={{ height: "30px" }}>製程敘述 : {name}</Typography>
          <div style={{ height: "115px", width: "100%" }}>
            <DynamicRadioButtonList
              data={processNameOptions}
              checked={value}
              handleChange={handleChange}
            />
          </div>
        </CardContent>
      </Card>
    </>
  );
}
