import React, { useState, useEffect } from "react";
import * as actions from "../../../../../../../_redux/dashboard/DashboardActions";

import { DynamicRadioButtonList } from "../../../../../../../../components/DynamicRadioButtonList";

import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";

import { shallowEqual, useDispatch, useSelector } from "react-redux";


export function Dashboard3Filter1({
  value,
  setValue,
  outboundTimeFilter,
  itemIdFilter,
  punchTypeFilter,
}) {
  const [name, setName] = useState("全部");
  // const [options, setOptions] = useState([]);
  // const [filteredData, setFilteredData] = useState([]);
  const dispatch = useDispatch();

  const {
    actionsLoading,
    productnameTimeData,
    pntProcessNameOptions,
    pntProcessNameFilteredData,
  } = useSelector(
    (state) => ({
      actionsLoading: state.dashboard.actionsLoading,
      productnameTimeData: state.dashboard.productnameTimeData,
      pntProcessNameOptions: state.dashboard.pntProcessNameOptions,
      pntProcessNameFilteredData: state.dashboard.pntProcessNameFilteredData,
    }),
    shallowEqual
  );

  useEffect(() => {
    dispatch(
      actions.getPntProcessNameOptions({
        filteredData: pntProcessNameFilteredData,
      })
    );
  }, [pntProcessNameFilteredData]);

  useEffect(() => {
    dispatch(
      actions.getPntProcessNameFilteredData({
        rawData: productnameTimeData,
        outboundTimeFilter: outboundTimeFilter,
        itemIdFilter: itemIdFilter,
        punchTypeFilter: punchTypeFilter,
      })
    );
  }, [productnameTimeData, outboundTimeFilter, itemIdFilter, punchTypeFilter]);

  const handleChange = (event) => {
    setValue(event.target.value);
    setName(event.target.name);
  };

  return (
    <>
      <Card raised={true}>
        <CardContent>
          <Typography style={{ height: "30px" }}>製程敘述 : {name}</Typography>
          <div style={{ height: "115px", width: "100%" }}>
            <DynamicRadioButtonList
              data={pntProcessNameOptions}
              checked={value}
              handleChange={handleChange}
            />
          </div>
        </CardContent>
      </Card>
    </>
  );
}
