import React, { useEffect, useCallback, useMemo, useRef } from "react";
import { VariableSizeList } from "react-window";
import { AutoSizer } from "react-virtualized";
import { useWindowResize } from "./useWindowResize";

import Radio from "@material-ui/core/Radio";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";

const Row = ({ data, index, setSize, windowWidth, handleChange, checked }) => {
  const rowRef = useRef();

  useEffect(() => {
    setSize(index, rowRef.current.getBoundingClientRect().height);
  }, [setSize, index, windowWidth]);

  return (
    <div ref={rowRef}>
      <Grid
        container
        direction="row"
        justifyContent="center"
        alignItems="center"
      >
        <Grid item xs={1.5}>
          <Radio
            value={data[index].value}
            name={data[index].label}
            key={index}
            onChange={handleChange}
            checked={checked === data[index].value}
          />
        </Grid>
        <Grid item xs>
          <Typography variant="body">{data[index].label}</Typography>
        </Grid>
      </Grid>
    </div>
  );
};

export function DynamicRadioButtonList({ data, checked, handleChange }) {
  const listRef = useRef();
  const sizeMap = useRef({});
  const setSize = useCallback((index, size) => {
    sizeMap.current = { ...sizeMap.current, [index]: size };
    listRef.current.resetAfterIndex(index);
  }, []);
  const getSize = (index) => sizeMap.current[index] || 50;
  const [windowWidth] = useWindowResize();

  return (
    <AutoSizer>
      {({ height, width }) => (
        <VariableSizeList
          ref={listRef}
          height={height}
          width={width}
          itemCount={data.length}
          itemSize={getSize}
          itemData={data}
        >
          {({ data, index, style }) => (
            <div style={style}>
              <Row
                data={data}
                index={index}
                setSize={setSize}
                windowWidth={windowWidth}
                handleChange={handleChange}
                checked={checked}
              />
            </div>
          )}
        </VariableSizeList>
      )}
    </AutoSizer>
  );
}
