import React, { useEffect } from "react";

import * as actions from "../../../../../../_redux/dashboard/DashboardActions";

import { Dashboard4Filter1 } from "./components/Dashboard4Filter1";
import { Dashboard4Filter2 } from "./components/Dashboard4Filter2";
import { Dashboard4Filter3 } from "./components/Dashboard4Filter3";
import { Dashboard4Filter4 } from "./components/Dashboard4Filter4";

import Stack from "@material-ui/core/Stack";
import { shallowEqual, useDispatch, useSelector } from "react-redux";
import { Grow } from "@material-ui/core";
import { Dashboard4FilterSkeletion, Dashboard4FilterTimeSkeletion } from "../Dashboard4CardSkeletion";
import { outboundTimeToISO } from "../../../../../../../components/toISOString";

export function Dashboard4Filters({
  processNameFilter,
  outboundTimeFilter,
  onClick,
  itemIdFilter,
  punchTypeFilter,
  setProcessNameFilter,
  setOutboundTimeFilter,
  setOnClick,
  setItemIdFilter,
  setPunchTypeFilter,
}) {
  const { actionsLoading } = useSelector(
    (state) => ({
      actionsLoading: state.dashboard.actionsLoading,
    }),
    shallowEqual
  );
  const dispatch = useDispatch();

  if (outboundTimeFilter[0] && outboundTimeFilter[1]) {
    var starttime = outboundTimeToISO(outboundTimeFilter[0],onClick);
    var endtime = outboundTimeToISO(outboundTimeFilter[1],onClick);
  }

  useEffect(() => {
    dispatch(
      actions.getDashboard4ChartData(
        starttime,
        endtime,
        processNameFilter,
        itemIdFilter,
        punchTypeFilter
      )
    );
  }, [outboundTimeFilter, processNameFilter, itemIdFilter, punchTypeFilter]);

  return (
    <>
      {actionsLoading ? (
        <Grow in={true} timeout={1500}>
          <Stack spacing={2}>
            <Dashboard4FilterSkeletion />
            <Dashboard4FilterTimeSkeletion/>
            <Dashboard4FilterSkeletion />
            <Dashboard4FilterSkeletion />
          </Stack>
        </Grow>
      ) : (
        <Stack spacing={2}>
          <Dashboard4Filter1
            value={processNameFilter}
            setValue={setProcessNameFilter}
            outboundTimeFilter={outboundTimeFilter}
            itemIdFilter={itemIdFilter}
            punchTypeFilter={punchTypeFilter}
          />
          <Dashboard4Filter2
            value={outboundTimeFilter}
            setValue={setOutboundTimeFilter}
            setOnClick={setOnClick}
          />
          <Dashboard4Filter3
            value={itemIdFilter}
            setValue={setItemIdFilter}
            processNameFilter={processNameFilter}
            outboundTimeFilter={outboundTimeFilter}
            punchTypeFilter={punchTypeFilter}
          />
          <Dashboard4Filter4
            value={punchTypeFilter}
            setValue={setPunchTypeFilter}
            processNameFilter={processNameFilter}
            outboundTimeFilter={outboundTimeFilter}
            itemIdFilter={itemIdFilter}
          />
        </Stack>
      )}
    </>
  );
}
