import React, { useState, useEffect } from "react";
import * as actions from "../../../../../_redux/dashboard/DashboardActions";
import { Chart5_1 } from "./chart5_1/Chart5_1";
import { Chart5_2 } from "./chart5_2/Chart5_2";
import { Chart5_3 } from "./chart5_3/Chart5_3";

import { Dashboard5Filters } from "./dashboard5Filter/Dashboard5Filters";

import Grid from "@material-ui/core/Grid";
import Card from "@material-ui/core/Card";
import Container from "@material-ui/core/Container";
import Box from "@material-ui/core/Box";

import Stack from "@material-ui/core/Stack";
import { CardContent, Typography } from "@material-ui/core";
import { shallowEqual, useDispatch, useSelector } from "react-redux";

export function Dashboard5Card() {
  const [proccessNameFilter, setProcessNameFilter] = useState("all");
  const [outboundTimeFilter, setOutboundTimeFilter] = useState([null, null]);
  const [onClick, setOnClick] = useState(false);
  const [itemIdFilter, setItemIdFilter] = useState("all");
  const [punchTypeFilter, setPunchTypeFilter] = useState("all");

  const dispatch = useDispatch();

  const {
    actionsLoading,
    processTimeData,

    dashboard5ChartData,
  } = useSelector(
    (state) => ({
      actionsLoading: state.dashboard.actionsLoading,
      processTimeData: state.dashboard.processTimeData,
      dashboard5ChartData: state.dashboard.dashboard5ChartData,
    }),
    shallowEqual
  );
  // new api
  useEffect(() => {
    dispatch(actions.getDashboard5ChartData());
  }, []);

  useEffect(() => {
    if (!processTimeData.length) {
      console.log("getProcessTimeData");
      dispatch(actions.getProcessTimeData());
    }
  }, []);

  useEffect(() => {
    let timeTemp = [];
    processTimeData.map((datum) => {
      timeTemp.push(datum.出站時間);
    });
    timeTemp.sort(function (a, b) {
      return new Date(a) - new Date(b);
    });
    let defaultTime = [
      new Date(new Date(timeTemp[0]).valueOf() + 1000 * 3600 * 24),
      new Date(
        new Date(
          new Date(timeTemp[timeTemp.length - 1]).getTime() + 24 * 60 * 60 * 999
        )
      ),
    ];
    setOutboundTimeFilter(defaultTime);
  }, [processTimeData]);

  return (
    <>
      <Stack spacing={3}>
        <Card raised={true}>
          <CardContent>
            <Typography sx={{ fontSize: 30 }}>機台使用效率</Typography>
          </CardContent>
        </Card>
        <Card>
          <Container maxWidth="false" sx={{ py: 2 }}>
            <Grid
              container
              direction="row"
              justifyContent="center"
              alignItems="center"
              spacing={3}
            >
              <Grid
                item
                xs={9}
                container
                direction="row"
                justifyContent="center"
                alignItems="center"
                spacing={2}
              >
                <Grid item xs={12} sm={12}>
                  <Chart5_1
                    data={dashboard5ChartData.chart1}
                    loading={actionsLoading}
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <Chart5_2
                    data={dashboard5ChartData.chart2}
                    loading={actionsLoading}
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <Chart5_3
                    data={dashboard5ChartData.chart3}
                    loading={actionsLoading}
                  />
                </Grid>
              </Grid>
              <Grid item xs={3}>
                <Dashboard5Filters
                  loading={actionsLoading}
                  processNameFilter={proccessNameFilter}
                  outboundTimeFilter={outboundTimeFilter}
                  onClick={onClick}
                  itemIdFilter={itemIdFilter}
                  punchTypeFilter={punchTypeFilter}
                  setProcessNameFilter={setProcessNameFilter}
                  setOutboundTimeFilter={setOutboundTimeFilter}
                  setOnClick={setOnClick}
                  setItemIdFilter={setItemIdFilter}
                  setPunchTypeFilter={setPunchTypeFilter}
                />
              </Grid>
            </Grid>
          </Container>
        </Card>
      </Stack>
    </>
  );
}
