import React, { useEffect, useState } from "react";

import Box from "@material-ui/core/Box";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import { ResponsivePie } from "@nivo/pie";
import { Dashboard5PieChartSkeletion } from "../Dashboard5CardSkeletion";
import { Grow } from "@material-ui/core";

export function Chart5_3({ data, loading }) {
  const [chartData, setChartData] = useState([]);
  useEffect(() => {
    if (data && typeof data !== "undefined") {
      setChartData(data);
    }
  }, [data]);

  return (
    <>
      <Grow in={true} timeout={1500}>
        <Card raised={true}>
          <CardContent style={{ height: 320 }}>
            <Typography
              sx={{
                fontSize: 20,
                fontWeight: 500,
                colors: "F5F5F5",
                textAlign: "center",
              }}
            >
              製程-人時(秒)/pcs
            </Typography>
            {loading ? (
              <Dashboard5PieChartSkeletion />
            ) : (
              <ResponsivePie
                data={chartData}
                sortByValue={true}
                margin={{ top: 25, right: 60, bottom: 35, left: 60 }}
                startAngle={270}
                endAngle={-360}
                activeOuterRadiusOffset={8}
                colors={[
                  "#FF8B37",
                  "#E94202",
                  "#EA6001",
                  "#FE7531",
                  "#FFAC51",
                  "#FF6902",
                  "#F2BD00",
                  "#FCD276",
                  "#F0F693",
                ]}
                borderWidth={1}
                borderColor={{ from: "color", modifiers: [["brighter", 3]] }}
                arcLinkLabel={function (e) {
                  return e.id + "－" + e.value;
                }}
                arcLinkLabelsTextColor={{ from: "color", modifiers: [] }}
                arcLinkLabelsThickness={3}
                arcLinkLabelsColor={{ from: "color" }}
                arcLinkLabelsDiagonalLength={8}
                arcLinkLabelsStraightLength={12}
                arcLabelsTextColor="#501701"
                arcLabelsRadiusOffset={0.65}
                arcLinkLabelsSkipAngle={2}
                arcLabelsSkipAngle={15}
                enableArcLabels={false}
                theme={{
                  fontSize: "17",
                  textColor: "#ffffff",
                }}
                isInteractive={false}
              />
            )}
          </CardContent>
        </Card>
      </Grow>
    </>
  );
}
