import React, { useState, useEffect } from "react";
import * as actions from "../../../../../../_redux/dashboard/DashboardActions";
import { Dashboard1Filter1 } from "./components/Dashboard1Filter1";
import { Dashboard1Filter2 } from "./components/Dashboard1Filter2";
import { Dashboard1Filter3 } from "./components/Dashboard1Filter3";
import { Dashboard1Filter4 } from "./components/Dashboard1Filter4";

import Stack from "@material-ui/core/Stack";

import { shallowEqual, useDispatch, useSelector } from "react-redux";
import {
  Dashboard1FilterSkeletion,
  Dashboard1FilterTimeSkeletion,
} from "../Dashboard1CardSkeletion";
import Grow from "@material-ui/core/Grow";
import {outboundTimeToISO} from "../../../../../../../components/toISOString";

export function Dashboard1Filters({
  processNameFilter,
  outboundTimeFilter,
  onClick,
  itemIdFilter,
  itemNameFilter,
  setProcessNameFilter,
  setOutboundTimeFilter,
  setOnClick,
  setItemIdFilter,
  setItemNameFilter,
}) {
  const { actionsLoading } = useSelector(
    (state) => ({
      actionsLoading: state.dashboard.actionsLoading,
    }),
    shallowEqual
  );
  const dispatch = useDispatch();

  if (outboundTimeFilter[0] && outboundTimeFilter[1]) {
    var starttime = outboundTimeToISO(outboundTimeFilter[0],onClick);
    var endtime = outboundTimeToISO(outboundTimeFilter[1],onClick);
  }

  useEffect(() => {
    dispatch(
      actions.getDashboard1ChartData(
        starttime,
        endtime,
        processNameFilter,
        itemIdFilter,
        itemNameFilter
      )
    );
  }, [outboundTimeFilter, processNameFilter, itemIdFilter, itemNameFilter]);

  return (
    <>
      {actionsLoading ? (
        <Grow in={true} timeout={1500}>
          <Stack spacing={2}>
            <Dashboard1FilterSkeletion />
            <Dashboard1FilterTimeSkeletion />
            <Dashboard1FilterSkeletion />
            <Dashboard1FilterSkeletion />
          </Stack>
        </Grow>
      ) : (
        <Stack spacing={2}>
          <Dashboard1Filter1
            value={processNameFilter}
            setValue={setProcessNameFilter}
            outboundTimeFilter={outboundTimeFilter}
            itemIdFilter={itemIdFilter}
            itemNameFilter={itemNameFilter}
          />
          <Dashboard1Filter2
            value={outboundTimeFilter}
            setValue={setOutboundTimeFilter}
            setOnClick={setOnClick}
          />
          <Dashboard1Filter3
            value={itemIdFilter}
            setValue={setItemIdFilter}
            processNameFilter={processNameFilter}
            outboundTimeFilter={outboundTimeFilter}
            itemNameFilter={itemNameFilter}
          />
          <Dashboard1Filter4
            value={itemNameFilter}
            setValue={setItemNameFilter}
            processNameFilter={processNameFilter}
            outboundTimeFilter={outboundTimeFilter}
            itemIdFilter={itemIdFilter}
          />
        </Stack>
      )}
    </>
  );
}
