import React, { useState, useEffect } from "react";

import * as actions from "../../../../../../_redux/dashboard/DashboardActions"

import { Dashboard5Filter1 } from "./components/Dashboard5Filter1";
import { Dashboard5Filter2 } from "./components/Dashboard5Filter2";
import { Dashboard5Filter3 } from "./components/Dashboard5Filter3";
import { Dashboard5Filter4 } from "./components/Dashboard5Filter4";

import Stack from "@material-ui/core/Stack";
import { shallowEqual, useDispatch, useSelector } from "react-redux";
import { Grow } from "@material-ui/core";
import { Dashboard5FilterSkeletion, Dashboard5FilterTimeSkeletion } from "../Dashboard5CardSkeletion";
import { outboundTimeToISO } from "../../../../../../../components/toISOString";

export function Dashboard5Filters({
  processNameFilter,
  outboundTimeFilter,
  onClick,
  itemIdFilter,
  punchTypeFilter,
  setProcessNameFilter,
  setOutboundTimeFilter,
  setOnClick,
  setItemIdFilter,
  setPunchTypeFilter,
}) {
  const { actionsLoading, processTimeData} = useSelector(
    (state) => ({
      actionsLoading: state.dashboard.actionsLoading,
      processTimeData: state.dashboard.processTimeData
    }),
    shallowEqual
  );
  const dispatch = useDispatch();

  if (outboundTimeFilter[0] && outboundTimeFilter[1]) {
    var starttime = outboundTimeToISO(outboundTimeFilter[0],onClick);
    var endtime = outboundTimeToISO(outboundTimeFilter[1],onClick);
  }

  useEffect(() => {
    dispatch(
      actions.getDashboard5ChartData(
        starttime,
        endtime,
        processNameFilter,
        itemIdFilter,
        punchTypeFilter
      )
    );
  }, [outboundTimeFilter, processNameFilter, itemIdFilter, punchTypeFilter]);


  useEffect(()=>{
    dispatch(
      actions.getPtFilteredData({
        rawData:processTimeData,
        processNameFilter:processNameFilter,
        outboundTimeFilter:outboundTimeFilter,
        itemIdFilter:itemIdFilter,
        punchTypeFilter:punchTypeFilter
      })
    )
  },[ 
    processTimeData,
    processNameFilter,
    outboundTimeFilter,
    itemIdFilter,
    punchTypeFilter])

  return (
    <>
    {actionsLoading?(
<Grow in={true} timeout={1500}>
      <Stack spacing={2}>
        <Dashboard5FilterSkeletion/>
        <Dashboard5FilterTimeSkeletion/>
        <Dashboard5FilterSkeletion/>
        <Dashboard5FilterSkeletion/>
      </Stack>
    </Grow>
    ):(
  <Stack spacing={2}>
      <Dashboard5Filter1
          value={processNameFilter}
          setValue={setProcessNameFilter}
          outboundTimeFilter={outboundTimeFilter}
          itemIdFilter={itemIdFilter}
          punchTypeFilter={punchTypeFilter}
        />
        <Dashboard5Filter2
          value={outboundTimeFilter}
          setValue={setOutboundTimeFilter}
          setOnClick={setOnClick}
        />
        <Dashboard5Filter3
          value={itemIdFilter}
          setValue={setItemIdFilter}
          processNameFilter={processNameFilter}
          outboundTimeFilter={outboundTimeFilter}
          punchTypeFilter={punchTypeFilter}
        />
        <Dashboard5Filter4
          value={punchTypeFilter}
          setValue={setPunchTypeFilter}
          processNameFilter={processNameFilter}
          outboundTimeFilter={outboundTimeFilter}
          itemIdFilter={itemIdFilter}
        />
      </Stack>
    )}
    
    
    </>
  );
}
