import React, { useEffect, useState } from "react";
import "../../../../../charts/chartsStyle.css"
import Chart from "react-apexcharts";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import { Dashboard2ChartSkeletion } from "../Dashboard2CardSkeletion";

export function Chart2_1({ loading,data }) {
  const [series, setSeries] = useState([]);
  const [options, setOptions] = useState(null);
  const [xAxis, setXAxis] = useState([]);

  useEffect(()=>{
    if(data && typeof data !== "undefined"){
    setSeries([{ name: "機時(秒)/pcs", data:data.details}])
    setXAxis(data.col)
    }
  },[data])

  useEffect(() => {
    setOptions({
      chart: {
        type: "bar",
        height: 350,
        offsetY: 10,
      },
      plotOptions: {
        bar: {
          horizontal: false,
          columnWidth: "55%",
          endingShape: "rounded",
          distributed: true,
        },
      },
      dataLabels: {
        enabled: true,
        style:{
          fontSize:"16px"
        }
      },
      title: {
        text: "跳間隔- 機時(秒)/pcs",
        align: "center",
        offsetX: 20,
        style: {
          fontSize: 20,
          fontWeight: 500,
          color: "#F5F5F5",
        },
      },
      stroke: {
        show: true,
        width: 2,
        colors: ["transparent"],
      },
      xaxis: {
        categories: xAxis,
        labels: {
          style: {
            fontSize: 16,
            colors: "#F5F5F5",
          },
        },
      },
      yaxis: {
        title: {
          text: "平均一片(s)",
          offsetX: -6,
          style: {
            color: "#ffffff",
            fontSize: "15px",
            fontWeight: 350,
          },
        },
        labels: {
          style: {
            fontSize: 16,
            colors: "#F5F5F5",
          },
        },
      },
      fill: {
        opacity: 1,
      },
      colors: ["#e85d04", "#F49011","#FFC108"],
    
      legend: {
        show:false,
        offsetY: 10,
        fontSize: 15,
      },
    });
  }, [xAxis]);

  return (
    <>
    {loading?(
      <Dashboard2ChartSkeletion/>
    ):(
      <Card raised={true}>
        <CardContent>
          {options ? (
            <Chart options={options} series={series} type="bar" height={290} />
          ) : null}
        </CardContent>
      </Card>
    )}
      
    </>
  );
}
