import React, { useEffect, useState } from "react";
import Chart from "react-apexcharts";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import "../../../../../charts/chartsStyle.css";
import { Dashboard4ChartSkeletion } from "../Dashboard4CardSkeletion";

export function Chart4_2({loading,data}) {
  const [series, setSeries] = useState([]);
  const [options, setOptions] = useState(null);

  useEffect(()=>{
    if(data && typeof data !== "undefined"){
    setSeries([{ name: "avg", data:data}])
    }
  },[data])

  useEffect(() => {
    setOptions({
      legend: {
        show: false,
      },
      chart: {
        height: 350,
        type: "treemap",
        animations:{
          enabled:false
        }
      },
      plotOptions: {
        treemap: {
          distributed: true,
          enableShades: false,
        },
      },
      dataLabels: {
        enabled: true,
        offsetY: -6,
        style: {
          fontSize: "20px",
        },
        formatter: function (text, op) {
          return [text, op.value];
        },
      },
      title: {
        text: "人時(秒)/pcs",
        align: "center",
        offsetX: 20,
        style: {
          fontSize: 20,
          fontWeight: 500,
          color: "#F5F5F5",
        },
      },

      colors: [
        "#FF7E00",
        "#EA6928",
        "#F2BD00",
        "#E74227",
        "#FFAD21",
        "#F8B979",
        "#9D2D02",
        "#C84D1F",
        "#B1552B",
        "#FD7324",
      ],
    });
  }, [series]);

  return (
    <>
      {loading ? (
          <Dashboard4ChartSkeletion />
      ) : (
        <Card raised={true}>
        <CardContent>
          {options ? (
            <Chart
              options={options}
              series={series}
              type="treemap"
              height={"660"}
            />
          ) : null}
        </CardContent>
      </Card>
      )}
    </>
  );
}
