import React, { useState, useEffect, useMemo } from "react";
import * as actions from "../../../../../_redux/dashboard/DashboardActions";

import { Chart1_1 } from "./chart1_1/Chart1_1";
import { Chart1_2 } from "./chart1_2/Chart1_2";
import { Chart1_3 } from "./chart1_3/Chart1_3";
import { Chart1_4 } from "./chart1_4/Chart1_4";

import Grid from "@material-ui/core/Grid";
import Card from "@material-ui/core/Card";
import Container from "@material-ui/core/Container";
import Stack from "@material-ui/core/Stack";
import { CardContent, Typography } from "@material-ui/core";
import { shallowEqual, useDispatch, useSelector } from "react-redux";
import { Dashboard1Filters } from "./dashboard1Filter/Dashboard1Filters";
import { sortArray } from "../../../../../../components/sortArray";

export function Dashboard1Card() {
  const [processNameFilter, setProcessNameFilter] = useState("all");
  const [outboundTimeFilter, setOutboundTimeFilter] = useState([null, null]);
  const [onClick, setOnClick] = useState(false);
  const [itemIdFilter, setItemIdFilter] = useState("all");
  const [itemNameFilter, setItemNameFilter] = useState("all");

  const dispatch = useDispatch();

  const { actionsLoading, manufacturingMethodData, dashboard1ChartData } =
    useSelector(
      (state) => ({
        actionsLoading: state.dashboard.actionsLoading,
        manufacturingMethodData: state.dashboard.manufacturingMethodData,
        dashboard1ChartData: state.dashboard.dashboard1ChartData,
      }),
      shallowEqual
    );
  // new api
  useEffect(() => {
    dispatch(actions.getDashboard1ChartData());
  }, []);
  sortArray(dashboard1ChartData)

  useEffect(() => {
    if (!manufacturingMethodData.length) {
      console.log("getManufacturingMethodData");
      dispatch(actions.getManufacturingMethodData());
    }
  }, []);

  useEffect(() => {
    let timeTemp = [];
    manufacturingMethodData.map((datum) => {
      timeTemp.push(datum.出站時間);
    });
    timeTemp.sort(function (a, b) {
      return new Date(a) - new Date(b);
    });
    let defaultTime = [
      new Date(new Date(timeTemp[0]).valueOf() + 1000 * 3600 * 24),
      new Date(
        new Date(
          new Date(timeTemp[timeTemp.length - 1]).getTime() + 24 * 60 * 60 * 999
        )
      ),
    ];
    setOutboundTimeFilter(defaultTime);
  }, [manufacturingMethodData]);

  return (
    <>
      <Stack spacing={3}>
        <Card raised={true}>
          <CardContent>
            <Typography sx={{ fontSize: 30 }}>
              【助撕、拉拔、斷法】- 機時/人時
            </Typography>
          </CardContent>
        </Card>
        <Card>
          <Container maxWidth="false" sx={{ py: 2 }}>
            <Grid
              container
              direction="row"
              justifyContent="center"
              alignItems="center"
              spacing={3}
            >
              <Grid
                item
                xs
                container
                direction="row"
                justifyContent="center"
                alignItems="center"
                spacing={2}
              >
                <Grid item xs={12} sm={6}>
                  <Chart1_1
                    data={dashboard1ChartData.chart1}
                    loading={actionsLoading}
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <Chart1_2
                    data={dashboard1ChartData.chart2}
                    loading={actionsLoading}
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <Chart1_3
                    data={dashboard1ChartData.chart3}
                    loading={actionsLoading}
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <Chart1_4
                    data={dashboard1ChartData.chart4}
                    loading={actionsLoading}
                  />
                </Grid>
              </Grid>
              <Grid item xs={3}>
                <Dashboard1Filters
                  loading={actionsLoading}
                  processNameFilter={processNameFilter}
                  outboundTimeFilter={outboundTimeFilter}
                  onClick={onClick}
                  itemIdFilter={itemIdFilter}
                  itemNameFilter={itemNameFilter}
                  setProcessNameFilter={setProcessNameFilter}
                  setOutboundTimeFilter={setOutboundTimeFilter}
                  setOnClick={setOnClick}
                  setItemIdFilter={setItemIdFilter}
                  setItemNameFilter={setItemNameFilter}
                />
              </Grid>
            </Grid>
          </Container>
        </Card>
      </Stack>
    </>
  );
}
