import React, { useState, useEffect } from "react";
import * as actions from "../../../../../_redux/dashboard/DashboardActions";

import { Chart4_1 } from "./chart4_1/Chart4_1";
import { Chart4_2 } from "./chart4_2/Chart4_2";

import { Dashboard4Filters } from "./dashboard4Filter/Dashboard4Filters";

import Grid from "@material-ui/core/Grid";
import Card from "@material-ui/core/Card";
import Container from "@material-ui/core/Container";

import Stack from "@material-ui/core/Stack";
import { CardContent, Typography } from "@material-ui/core";

import { shallowEqual, useDispatch, useSelector } from "react-redux";

export function Dashboard4Card() {
  const [proccessNameFilter, setProcessNameFilter] = useState("all");
  const [outboundTimeFilter, setOutboundTimeFilter] = useState([null, null]);
  const [onClick, setOnClick] = useState(false);
  const [itemIdFilter, setItemIdFilter] = useState("all");
  const [punchTypeFilter, setPunchTypeFilter] = useState("None");

  const dispatch = useDispatch();

  const { actionsLoading, productnameTimeData, dashboard4ChartData } =
    useSelector(
      (state) => ({
        actionsLoading: state.dashboard.actionsLoading,
        productnameTimeData: state.dashboard.productnameTimeData,
        dashboard4ChartData: state.dashboard.dashboard4ChartData,
      }),
      shallowEqual
    );
  // new api
  useEffect(() => {
    dispatch(actions.getDashboard4ChartData());
  }, []);

  useEffect(() => {
    if (!productnameTimeData.length) {
      console.log("getProductnameTimeData");
      dispatch(actions.getProductnameTimeData());
    }
  }, []);
console.log(dashboard4ChartData)
  useEffect(() => {
    let timeTemp = [];
    productnameTimeData.map((datum) => {
      timeTemp.push(datum.出站時間);
    });
    timeTemp.sort(function (a, b) {
      return new Date(a) - new Date(b);
    });
    let defaultTime = [
      new Date(new Date(timeTemp[0]).valueOf() + 1000 * 3600 * 24),
      new Date(
        new Date(
          new Date(timeTemp[timeTemp.length - 1]).getTime() + 24 * 60 * 60 * 999
        )
      ),
    ];
    setOutboundTimeFilter(defaultTime);
  }, [productnameTimeData]);

  return (
    <>
      <Stack spacing={3}>
        <Card raised={true}>
          <CardContent>
            <Typography sx={{ fontSize: 30 }}>【料號】- 機時/人時</Typography>
          </CardContent>
        </Card>
        <Card>
          <Container maxWidth="false" sx={{ py: 2 }}>
            <Grid
              container
              direction="row"
              justifyContent="center"
              alignItems="center"
              spacing={3}
            >
              <Grid
                item
                xs
                container
                direction="row"
                justifyContent="center"
                alignItems="center"
                spacing={2}
              >
                <Grid item xs={12} sm={6}>
                  <Chart4_1
                    data={dashboard4ChartData.chart1}
                    loading={actionsLoading}
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <Chart4_2
                    data={dashboard4ChartData.chart2}
                    loading={actionsLoading}
                  />
                </Grid>
              </Grid>
              <Grid item xs={3}>
                <Dashboard4Filters
                  loading={actionsLoading}
                  processNameFilter={proccessNameFilter}
                  outboundTimeFilter={outboundTimeFilter}
                  onClick={onClick}
                  itemIdFilter={itemIdFilter}
                  punchTypeFilter={punchTypeFilter}
                  setProcessNameFilter={setProcessNameFilter}
                  setOutboundTimeFilter={setOutboundTimeFilter}
                  setOnClick={setOnClick}
                  setItemIdFilter={setItemIdFilter}
                  setPunchTypeFilter={setPunchTypeFilter}
                />
              </Grid>
            </Grid>
          </Container>
        </Card>
      </Stack>
    </>
  );
}
