import React, { useState, useEffect } from "react";
import * as actions from "../../../../../../../_redux/dashboard/DashboardActions";

import { DynamicRadioButtonList } from "../../../../../../../../components/DynamicRadioButtonList";

import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";

import { shallowEqual, useDispatch, useSelector } from "react-redux";

import { VariableSizeList } from "react-window";

export function Dashboard5Filter3({
  value,
  setValue,
  processNameFilter,
  outboundTimeFilter,
  punchTypeFilter,
}) {
  const [name, setName] = useState("全部");

  const dispatch = useDispatch();

  const { actionsLoading, processTimeData, ptItemIdOptions,ptItemIdFilteredData } =
    useSelector(
      (state) => ({
        actionsLoading: state.dashboard.actionsLoading,
        processTimeData: state.dashboard.processTimeData,
        ptItemIdOptions: state.dashboard.ptItemIdOptions,
        ptItemIdFilteredData:state.dashboard.ptItemIdFilteredData
      }),
      shallowEqual
    );



  useEffect(() => {
    dispatch(actions.getPtItemIdOptions({ filteredData: ptItemIdFilteredData}));
  }, [ptItemIdFilteredData]);

  useEffect(() => {
    dispatch(
      actions.getPtItemIdFilteredData({
        rawData: processTimeData,
        processNameFilter: processNameFilter,
        outboundTimeFilter: outboundTimeFilter,
        punchTypeFilter: punchTypeFilter,
      })
    );
  }, [
    processTimeData,
    processNameFilter,
    outboundTimeFilter,
    punchTypeFilter,
  ]);

  const handleChange = (event) => {
    setValue(event.target.value);
    setName(event.target.name);
  };

  return (
    <>
      <Card raised={true}>
        <CardContent>
          <Typography style={{ height: "30px" }}>料號 : {name}</Typography>
          <div style={{ height: "115px", width: "100%" }}>
            <DynamicRadioButtonList
              data={ptItemIdOptions}
              checked={value}
              handleChange={handleChange}
            />
          </div>
        </CardContent>
      </Card>
    </>
  );
}