import React, { useState, useEffect } from "react";
import * as actions from "../../../../../../../_redux/dashboard/DashboardActions";

import { DynamicRadioButtonList } from "../../../../../../../../components/DynamicRadioButtonList";

import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import Radio from "@material-ui/core/Radio";

import { shallowEqual, useDispatch, useSelector } from "react-redux";


export function Dashboard5Filter1({
  value,
  setValue,
  outboundTimeFilter,
  itemIdFilter,
  punchTypeFilter,
}) {
  const [name, setName] = useState("全部");

  const dispatch = useDispatch();

  const {
    actionsLoading,
    processTimeData,
    ptProcessNameOptions,
    ptProcessNameFilteredData,
  } = useSelector(
    (state) => ({
      actionsLoading: state.dashboard.actionsLoading,
      processTimeData: state.dashboard.processTimeData,
      ptProcessNameOptions: state.dashboard.ptProcessNameOptions,
      ptProcessNameFilteredData: state.dashboard.ptProcessNameFilteredData,
    }),
    shallowEqual
  );

  useEffect(() => {
    dispatch(
      actions.getPtProcessNameOptions({
        filteredData: ptProcessNameFilteredData,
      })
    );
  }, [ptProcessNameFilteredData]);

  useEffect(() => {
    dispatch(
      actions.getPtProcessNameFilteredData({
        rawData: processTimeData,
        outboundTimeFilter: outboundTimeFilter,
        itemIdFilter: itemIdFilter,
        punchTypeFilter: punchTypeFilter,
      })
    );
  }, [processTimeData, outboundTimeFilter, itemIdFilter, punchTypeFilter]);

  const handleChange = (event) => {
    setValue(event.target.value);
    setName(event.target.name);
  };

  return (
    <>
      <Card raised={true}>
        <CardContent>
          <Typography style={{ height: "30px" }}>製程敘述 : {name}</Typography>
          <div style={{ height: "115px", width: "100%" }}>
            <DynamicRadioButtonList
              data={ptProcessNameOptions}
              checked={value}
              handleChange={handleChange}
            />
          </div>
        </CardContent>
      </Card>
    </>
  );
}
