import React, { useState, useEffect } from "react";
import * as actions from "../../../../../../../_redux/dashboard/DashboardActions";

import { DynamicRadioButtonList } from "../../../../../../../../components/DynamicRadioButtonList";

import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";

import { shallowEqual, useDispatch, useSelector } from "react-redux";


export function Dashboard4Filter4({
  value,
  setValue,
  processNameFilter,
  outboundTimeFilter,
  itemIdFilter,
}) {
  const [name, setName] = useState("None");

  const dispatch = useDispatch();

  const {
    actionsLoading,
    productnameTimeData,
    pntPunchTypeOptions,
    pntPunchTypeFilteredData,
  } = useSelector(
    (state) => ({
      actionsLoading: state.dashboard.actionsLoading,
      productnameTimeData: state.dashboard.productnameTimeData,
      pntPunchTypeOptions: state.dashboard.pntPunchTypeOptions,
      pntPunchTypeFilteredData: state.dashboard.pntPunchTypeFilteredData,
    }),
    shallowEqual
  );

  useEffect(() => {
    dispatch(
      actions.getPntPunchTypeOptions({ filteredData: pntPunchTypeFilteredData })
    );
  }, [pntPunchTypeFilteredData]);

  useEffect(() => {
    dispatch(
      actions.getPntPunchTypeFilteredData({
        rawData: productnameTimeData,
        processNameFilter: processNameFilter,
        outboundTimeFilter: outboundTimeFilter,
        itemIdFilter: itemIdFilter,
      })
    );
  }, [
    productnameTimeData,
    processNameFilter,
    outboundTimeFilter,
    itemIdFilter,
  ]);

  const handleChange = (event) => {
    setValue(event.target.value);
    setName(event.target.name);
  };

  return (
    <>
      <Card raised={true}>
        <CardContent>
          <Typography style={{ height: "30px" }}>沖型規格 : {name}</Typography>
          <div style={{ height: "115px", width: "100%" }}>
            <DynamicRadioButtonList
              data={pntPunchTypeOptions}
              checked={value}
              handleChange={handleChange}
            />
          </div>
        </CardContent>
      </Card>
    </>
  );
}

