import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/styles";
import Grid from "@material-ui/core/Grid";
import Card from "@material-ui/core/Card";
import Container from "@material-ui/core/Container";
import Stack from "@material-ui/core/Stack";
import Skeleton from "@material-ui/core/Skeleton";
import Grow from "@material-ui/core/Grow";
import { CardContent, Typography } from "@material-ui/core";
import Box from "@material-ui/core/Box";
import Avatar from "@material-ui/core/Avatar";

const useStyles = makeStyles({
  container: {
    paddingTop: 10,
    borderRadius: 10,
  },
  contentText: {
    fontSize: 16,
    marginBottom: 10,
    textAlign: "center",
  },
  filterContainer: {
    paddingTop: 3,
    borderRadius: 10,
  },
  filterContentText: {
    fontSize: 16,
    marginBottom: 3,
  },
});

export function Dashboard5ChartSkeletion({ variant }) {
  const classes = useStyles();

  return (
    <Grow in={true} timeout={1500}>
      <Card className={classes.container} raised={true}>
        <CardContent>
          <>
            <Typography className={classes.contentText}>
              <Skeleton animation="wave" width="100%" height={50} />
            </Typography>
            <Typography className={classes.contentText}>
              <Skeleton
                variant="rectangular"
                animation="wave"
                width="100%"
                height={250}
              />
            </Typography>
          </>
        </CardContent>
      </Card>
    </Grow>
  );
}

export function Dashboard5PieChartSkeletion({ variant }) {
  const classes = useStyles();

  return (
  
      <Box
        sx={{
          // bgcolor: '#121212',
          p: 3,
          width: "100%",
          display: "flex",
          justifyContent: "center",
        }}
      >
        <Skeleton variant="circular" animation="wave" width={220} height={220} />
      </Box>
   
  );
}

export function Dashboard5FilterSkeletion({ variant }) {
  const classes = useStyles();

  return (
    <Grow in={true} timeout={1500}>
      <Card className={classes.filterContainer} raised={true}>
        <CardContent>
          <>
            <Typography className={classes.filterContainerText}>
              <Skeleton animation="wave" width="30%" />
            </Typography>
            <Box sx={{ display: "flex", alignItems: "center" }}>
              <Box sx={{ margin: 1 }}>
                <Skeleton variant="circular" width={20} height={20}>
                  <Avatar />
                </Skeleton>
              </Box>
              <Box sx={{ width: "100%", margin: 1 }}>
                <Skeleton width="38%">
                  <Typography>.</Typography>
                </Skeleton>
              </Box>
            </Box>
            <Box sx={{ display: "flex", alignItems: "center" }}>
              <Box sx={{ margin: 1 }}>
                <Skeleton variant="circular" width={20} height={20}>
                  <Avatar />
                </Skeleton>
              </Box>
              <Box sx={{ width: "100%", margin: 1 }}>
                <Skeleton width="65%">
                  <Typography>.</Typography>
                </Skeleton>
              </Box>
            </Box>
            <Box sx={{ display: "flex", alignItems: "center" }}>
              <Box sx={{ margin: 1 }}>
                <Skeleton variant="circular" width={20} height={20}>
                  <Avatar />
                </Skeleton>
              </Box>
              <Box sx={{ width: "100%", margin: 1 }}>
                <Skeleton width="55%">
                  <Typography>.</Typography>
                </Skeleton>
              </Box>
            </Box>
          </>
        </CardContent>
      </Card>
    </Grow>
  );
}

export function Dashboard5FilterTimeSkeletion({ variant }) {
  const classes = useStyles();

  return (
    <Grow in={true} timeout={1500}>
      <Card className={classes.filterContainer} raised={true}>
        <CardContent>
            <>
                <Box sx={{ display: "flex", alignItems: "center" }}>
                <Box sx={{  width: "60%"  }}>
                <Skeleton
                  variant="rectangular"
                  animation="wave"
                  width="100%"
                  height={55}
                />
                </Box>
                <Box sx={{padding:2}}>
                  to
                  </Box>
                <Box sx={{ width: "60%" }}>
                <Skeleton
                  variant="rectangular"
                  animation="wave"
                  width="100%"
                  height={50}
                />
                </Box>
              </Box>
            </>
        </CardContent>
      </Card>
    </Grow>
  );
}
