import React, { useState, useEffect } from "react";
import * as actions from "../../../../../../../_redux/dashboard/DashboardActions";

import { DynamicRadioButtonList } from "../../../../../../../../components/DynamicRadioButtonList";

import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";

import { shallowEqual, useDispatch, useSelector } from "react-redux";

import { VariableSizeList } from "react-window";

export function Dashboard5Filter4({
  value,
  setValue,
  processNameFilter,
  outboundTimeFilter,
  itemIdFilter,
}) {
  const [name, setName] = useState("全部");

  const dispatch = useDispatch();

  const {
    actionsLoading,
    processTimeData,
    ptPunchTypeOptions,
    ptPunchTypeFilteredData,
  } = useSelector(
    (state) => ({
      actionsLoading: state.dashboard.actionsLoading,
      processTimeData: state.dashboard.processTimeData,
      ptPunchTypeOptions: state.dashboard.ptPunchTypeOptions,
      ptPunchTypeFilteredData: state.dashboard.ptPunchTypeFilteredData,
    }),
    shallowEqual
  );

  useEffect(() => {
    dispatch(
      actions.getPtPunchTypeOptions({ filteredData: ptPunchTypeFilteredData })
    );
  }, [ptPunchTypeFilteredData]);

  useEffect(() => {
    dispatch(
      actions.getPtPunchTypeFilteredData({
        rawData: processTimeData,
        processNameFilter: processNameFilter,
        outboundTimeFilter: outboundTimeFilter,
        itemIdFilter: itemIdFilter,
      })
    );
  }, [
    processTimeData,
    processNameFilter,
    outboundTimeFilter,
    itemIdFilter,
  ]);

  const handleChange = (event) => {
    setValue(event.target.value);
    setName(event.target.name);
  };

  return (
    <>
      <Card raised={true}>
        <CardContent>
          <Typography style={{ height: "30px" }}>沖型規格 : {name}</Typography>
          <div style={{ height: "115px", width: "100%" }}>
            <DynamicRadioButtonList
              data={ptPunchTypeOptions}
              checked={value}
              handleChange={handleChange}
            />
          </div>
        </CardContent>
      </Card>
    </>
  );
}
