import React, { useEffect, useState } from "react";
import Chart from "react-apexcharts";
import "../../../../../charts/chartsStyle.css";
import Box from "@material-ui/core/Box";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import { Dashboard5ChartSkeletion } from "../Dashboard5CardSkeletion";

export function Chart5_1({ data,loading }) {
  const [series, setSeries] = useState([]);
  const [options, setOptions] = useState(null);
  const [xAxis, setXAxis] = useState([]);

  useEffect(()=>{
    if(data && typeof data !== "undefined"){
    setSeries([{ name: "機時(秒)/pcs", data:data.details}])
    setXAxis(data.col)
    }
  },[data])

  useEffect(() => {
    setOptions({
      chart: {
        type: "bar",
        height: 350,
        offsetY: 10,
      },
      plotOptions: {
        bar: {
          horizontal: false,
          columnWidth: "55%",
          endingShape: "rounded",
          distributed: true,
          dataLabels: {
            position: "top",
          },
        },
      },
      dataLabels: {
        enabled: true,
        offsetY: -19,
        style: {
          fontSize: "13px",
          color: "#2F2F2F",
        },
        dropShadow: {
          enabled: true,
          top: 3,
          left: 1,
          blur: 1,
          color: "#000",
          opacity: 0.5,
        },
      },
      title: {
        text: "機台-機時(秒)/pcs",
        align: "center",
        offsetX: 20,
        style: {
          fontSize: 20,
          fontWeight: 500,
          color: "#F5F5F5",
        },
      },
      stroke: {
        show: true,
        width: 2,
        colors: ["transparent"],
      },
      xaxis: {
        categories: xAxis,
        labels: {
          style: {
            fontSize: 16,
            colors: "#F5F5F5",
          },
        },
      },
      yaxis: {
        labels: {
          style: {
            fontSize: 16,
            colors: "#F5F5F5",
          },
        },
      },
      fill: {
        opacity: 1,
      },
      // theme: {
      //   palette: 'palette9'
      // },
      colors: [
        "#FF7E00",
        "#EA6928",
        "#F2BD00",
        "#E74227",
        "#FFAD21",
        "#F8B979",
        "#9D2D02",
        "#C84D1F",
        "#B1552B",
        "#FD7324",
      ],

      legend: {
        show: false,
        offsetY: 10,
        fontSize: 15,
      },
    });
  }, [xAxis]);
  return (
    <>
      {loading ? (
        <Dashboard5ChartSkeletion />
      ) : (
        <Card raised={true}>
          <CardContent>
            {options ? (
              <Chart
                options={options}
                series={series}
                type="bar"
                height={320}
              />
            ) : null}
          </CardContent>
        </Card>
      )}
    </>
  );
}
