import { format } from "date-fns";
import React from "react";

export function outboundTimeToISO(outboundTime, onClick) {

  function pad(number) {
    if (number < 10) {
      return "0" + number;
    }
    return number;
  }
  if (onClick == false) {
    outboundTime =
      outboundTime.getUTCFullYear() +
      "-" +
      pad(outboundTime.getUTCMonth() + 1) +
      "-" +
      pad(outboundTime.getUTCDate());
  } else {
    outboundTime =
      outboundTime.getUTCFullYear() +
      "-" +
      pad(outboundTime.getUTCMonth() + 1) +
      "-" +
      pad(outboundTime.getUTCDate() + 1);
  }
  return outboundTime;
}

